import { withRouter } from 'next/router';

const Trips = ({ router }) => {
  const { count } = router.query;
  console.log(count);
  const countToNum = count ? count.parseInt(count, 10) : 0;
  return (
    <>
      <h1>my trips</h1>
      <button
        onClick={() => {
          router.push({
            pathname: '/my-allegiant/trips',
            query: { count: countToNum + 1 },
          });
        }}
      >
        +1
      </button>
      {count && <p>{`Count = ${count}`}</p>}
    </>
  );
};

export default withRouter(Trips);
